<template>
    <div>
        <h1>System</h1>

        <ul>
            <li><router-link v-on:click.native="activateTabs" to="/">Dashboard</router-link></li>
            <li><router-link v-on:click.native="activateTabs" to="/about">About</router-link></li>
        </ul>

        <h1 class="link" v-on:click="addClient">
            Clients
            <i class="el-icon-circle-plus-outline"></i>
        </h1>

        <ul>
            <li v-for="client in clients" v-bind:key="client.id">
                <router-link 
                    v-on:click.native="activateTabs" 
                    v-bind:to="{ 
                        name: 'client', 
                        params: { 
                            id: client.id 
                        } 
                    }">
                        
                    {{ client.title }}
                </router-link>
            </li>
        </ul>
        
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            loading: false,
            clients: null,
            error: null,
            clientName: ''
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.error = this.clients = null
            this.loading = true

            axios
                .get('http://localhost:3000/client')
                .then( response => {
                    this.clients = response.data
                    this.loading = false
                    //console.log(response)
                })
                .catch( error => {
                    this.loading = false;
                    this.error = error.message
                })
        },
        addClient () {
            this.$prompt('Name of new client','Client',{
                confirmButtonText: 'Add',
                cancelButtonText: 'Cancel'
            }).then(({value}) => {
                this.clientName = value
                if( this.clientName )
                {
                    axios
                        .post(
                            'http://localhost:3000/client',
                            {
                                name: this.clientName
                            }
                        )
                        .then(response => {
                            this.fetchData()
                        })
                }
            }).catch(()=>{
                //
            })
            // if( this.clientName )
            // {
            //     axios
            //         .post(
            //             'http://localhost:3000/client',
            //             {
            //                 name: this.clientName
            //             }
            //         )
            //         .then(response => {
            //             this.fetchData()
            //         })
            // }
        },
        activateTabs () {
            this.$root.$data.activeDivs = [ 'clients','projects' ]
        }
    }
}
</script>

<style>
#clients a.router-link-exact-active.router-link-active {
    font-weight: 900;
    color: orange;
}
#clients h1 {
    color: #aaa;
}
#clients li a {
    color: #555;
}
</style>

