<template>
    <div>
        <h1>{{ title }}</h1>

        <el-button v-on:click="remove">Remover</el-button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            id: '',
            title: ''
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.id = this.$route.params.projectid

            axios
                .get( 'http://localhost:3000/project/' + this.id + '/details' )
                .then( response => {
                    this.title = response.data[0].title
                })
        }, // fetchData
        remove () {
            this.$confirm(
                'This will permanently delete the project. Continue?',
                'Warning',
                {
                    confirmButtonText: 'Delete',
                    cancelButtonText: 'Cancel',
                    type: 'warning'
                }
            )
            .then(()=>{
                axios.post( 'http://localhost:3000/project/remove', {
                    id: this.id,
                    _method: 'delete'
                })
                .then( response => {
                    this.$root.$data.activeDivs = [ 'clients', 'projects' ]
                    this.$root.$router.push({ 
                        name: 'client', 
                        params: { 
                            id: this.$route.params.id
                        } 
                    })
                    this.$message({
                        type: 'success',
                        message: 'Delete completed'
                    })
                }) // axios.post
            }) 
            .catch(()=>{
                this.$message({
                    type: 'info',
                    message: 'Delete canceled'
                })
            })// this.confirm
        } // remove
    }
}
</script>
