<template>
    <div class="page-tasks">
        <h1 class="link" v-on:click="addTask()">
            Tasks
            <i class="el-icon-circle-plus-outline"></i>
        </h1>
        
        <div v-if="tasks.length">
            <ul>
                <li v-for="task in tasks" v-bind:key="task.id">
                    <router-link
                        v-on:click.native="activateTabs()"
                        v-bind:to="{ 
                            name: 'task', 
                            params: { 
                                id: clientId,
                                projectid: projectId,
                                taskid: task.id
                            } 
                        }">
                        
                        <span>[#{{ task.id }}]</span> {{ task.title }}
                    </router-link>
                </li>
            </ul>
        </div>
        <div v-else>
            Empty
        </div>

        <br>

        <router-link 
            v-on:click.native="activateTabsBack()"
            v-bind:to="{ 
                name: 'client', 
                params: { 
                    id:this.$route.params.id,
                }
            }">
            
            voltar
        </router-link>


    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            taskName: '',
            projectId: '',
            clientId: '',
            tasks: {}
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.projectId = this.$route.params.projectid
            this.clientId = this.$route.params.id

            axios
                .get( 'http://localhost:3000/task/project/' + this.projectId )
                .then( response => {
                    this.tasks = response.data
                })
        },
        addTask () {
            this.$prompt('Title of new task','Task',{
                confirmButtonText: 'Add',
                cancelButtonText: 'Cancel'
            }).then(({value}) => {
                this.taskName = value
                if( this.projectId && this.taskName )
                {
                    axios
                        .post(
                            'http://localhost:3000/task',
                            {
                                projectId: this.projectId,
                                taskName: this.taskName
                            }
                        )
                        .then(response => {
                            this.fetchData()
                            this.$message({
                                type: 'success',
                                message: 'Add completed'
                            })
                        })
                }
            }).catch(()=>{
                this.$message({
                    type: 'info',
                    message: 'Add canceled'
                })
            })
        },
        activateTabs() {
            this.$root.$data.activeDivs = [ 'task' ]
        },
        activateTabsBack() {
            this.$root.$data.activeDivs = [ 'clients','projects' ]
        }
    }
}
</script>

<style>
    .page-tasks {
        position: absolute;
        top: 10px;
        width: 280px;
    }
    #tasks li a span,
    #tasks h1 {
        color: #777
    }
    #tasks,
    #tasks li a {
        color: #555;
    }

</style>
