<template>
    <div>
        <h1>{{ name }}</h1>
        {{ description }}
        <div v-show="!edit" v-on:click="edit = !edit"><i class="material-icons">edit</i></div>
        <div v-show="edit">
            <textarea v-model="description"></textarea><br>
            <button v-on:click="update">Salvar</button>
        </div>
        <br>
        <br>
        <el-button v-on:click="remove">Delete</el-button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            id: '',
            name: '',
            description: '',
            edit: false
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.id = this.$route.params.id
            axios
                .get( 'http://localhost:3000/client/' + this.id )
                .then( response => {
                    this.name = response.data[0].title
                    this.description = response.data[0].description
                })
        },
        update () {
            axios
                .post('http://localhost:3000/client', {
                    id: this.id,
                    description: this.description,
                    _method: 'put'
                })
                .then(response=>{
                    this.edit = !this.edit
                    this.fetchData()
                })
        },
        remove () {
            this.$confirm(
                'This will permanently delete the project. Continue?',
                'Warning',
                {
                    confirmButtonText: 'Delete',
                    cancelButtonText: 'Cancel',
                    type: 'warning'
                }
            )
            .then(()=>{
                axios.post( 'http://localhost:3000/client/remove', {
                    id: this.id,
                    _method: 'delete'
                })
                .then( response => {
                    document.location.hash = '/'
                    this.$message({
                        type: 'success',
                        message: 'Delete completed'
                    })
                })
            })
            .catch(()=>{
                this.$message({
                    type: 'info',
                    message: 'Delete canceled'
                })
            })
        }
    }
}
</script>


<style>
#projects h1 {
    color: #999;
}
#projects li a {
    color: #555;
}
</style>

