<template>
    <div class="project-page">
        <h1 class="link" v-on:click="addProject()">
            Projects
            <i class="el-icon-circle-plus-outline"></i>
        </h1>

        <div v-if="projects.length">
            <ul>
                <li v-for="project in projects" v-bind:key="project.id">
                    <router-link v-on:click.native="activateTabs" v-bind:to="{ name: 'project', params: { id: clientId, projectid: project.id } }">{{ project.title }}</router-link>
                </li>
            </ul>
        </div>
        <div v-else>
            Esse client ainda não possui nenhum project
        </div>
        <br>

    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            projectName: '',
            clientId: '',
            projects: {}
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.clientId = this.$route.params.id

            axios
                .get( 'http://localhost:3000/project/' + this.clientId )
                .then( response => {
                    this.projects = response.data
                })
        },
        addProject () {
            this.$prompt('Title of new project','Project',{
                confirmButtonText: 'Add',
                cancelButtonText: 'Cancel'
            }).then(({value}) => {
                this.projectName = value
                if( this.projectName && this.clientId )
                {
                    axios
                        .post(
                            'http://localhost:3000/project',
                            {
                                clientId: this.clientId,
                                projectName: this.projectName
                            }
                        )
                        .then(response => {
                            this.fetchData()
                        })
                }
            }).catch(()=>{
                //
            })
        },
        activateTabs() {
            this.$root.$data.activeDivs = [ 'tasks' ]
        }
    }
}
</script>


