<template>
	<div id="app">

		<div id="clients" v-bind:class="{ active: clientsIsActive }">
			<Menu/>
		</div>

		<div id="projects" v-bind:class="{ active: projectsIsActive }">
			<transition>
				<router-view :key="$route.fullPath"/>
			</transition>
		</div>

		<div id="tasks" v-bind:class="{ active: tasksIsActive, hidden: taskIsActive }">
			<transition>
				<router-view name="tasks" :key="$route.fullPath"/>
			</transition>
		</div>

		<div id="task" v-bind:class="{ active: taskIsActive, hidden: tasksIsActive }">
			<router-view name="task"/>
		</div>

		<div id="default-content" v-bind:class="{ large: tasksIsActive }">
			<transition>
				<router-view name="content" :key="$route.fullPath"/>
			</transition>
		</div>

	</div><!-- #app -->
</template>

<script>
import Menu from './components/Menu.vue'

export default {
	data () {
		return {
			clientsIsActive: false,
			projectsIsActive: false,
			tasksIsActive: false,
			taskIsActive: false
		}
	},
	components: {
		Menu
	},
	created () {
		this.getActiveTabs()
	},
	watch: {
		'$route': 'getActiveTabs'
	},
	methods: {
		getActiveTabs () {
			var activeTabs = this.$root.$data.activeDivs
			this.clientsIsActive = ( activeTabs.indexOf('clients') == -1 )?false:true
			this.projectsIsActive = ( activeTabs.indexOf('projects') == -1 )?false:true
			this.tasksIsActive = ( activeTabs.indexOf('tasks') == -1 )?false:true
			this.taskIsActive = ( activeTabs.indexOf('task') == -1 )?false:true
		}
	}

}
</script>


