<template>
    <div>
        <h1>{{ taskTitle }}</h1>

        <p>{{ taskStatus }}</p>
        <br>
        <br>
        <el-button 
            v-on:click="remove" 
            type="danger" 
            plain>
            
            Delete
        </el-button>
        <el-button
            v-on:click="back">
            Back
        </el-button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            taskTitle: '',
            taskStatus: '',
            taskId: null
        }
    },
    created () {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.taskId = this.$route.params.taskid

            axios
                .get( 'http://localhost:3000/task/' + this.taskId )
                .then( response => {
                    this.taskTitle = response.data[0].title
                    this.taskStatus = response.data[0].status
                })
        },
        activateTabs() {
            this.$root.$data.activeDivs = [ 'tasks' ]
        },
        remove () {
            this
                .$confirm(
                    'This will permanently delete the task. Continue?',
                    'Warning',
                    {
                        confirmButtonText: 'Delete',
                        cancelButtonText: 'Cancel',
                        type: 'warning'
                    }
                )
                .then(()=>{
                    axios
                        .post( 'http://localhost:3000/task/remove', {
                            id: this.taskId,
                            _method: 'delete'
                        })
                        .then( response => {
                            this.$root.$data.activeDivs = [ 'tasks' ]
                            this.$root.$router.push({ 
                                name: 'project', 
                                params: { 
                                    id: this.$route.params.id,
                                    projectid: this.$route.params.projectid
                                } 
                            })
                            this.$message({
                                type: 'success',
                                message: 'Delete completed'
                            })
                        })
                })
                .catch(()=>{
                    this
                        .$message({
                            type: 'info',
                            message: 'Delete canceled'
                        })
                })
        },
        back () {
            this.activateTabs()
            this.$root.$router.push({ 
                name: 'project', 
                params: { 
                    id:this.$route.params.id,
                    tasksid:this.taskId
                }
            })
        }
    }
}
</script>
